  describe('Error Handling', () => {
    it('should handle missing elements gracefully', async () => {
      const { fillAndSubmitForm } = require('./e2eHelpers');
      const mockPage = createMockPage();
      
      // Mock locator that returns 0 count (element not found)
      const mockLocator = createMockLocator({ count: jest.fn().mockResolvedValue(0) });
      (mockPage.locator as jest.Mock).mockReturnValue(mockLocator);
      
      // Should throw error when form field not found
      await expect(
        fillAndSubmitForm(mockPage as Page, { firstName: 'John' }, 'Submit')
      ).rejects.toThrow('Form field not found');
    });
  });

