describe('Helper Function Documentation', () => {
  it('should have JSDoc comments for all exported functions', () => {
    const fs = require('fs');
    const path = require('path');
    
    const helpersPath = path.join(__dirname, 'e2eHelpers.ts');
    const content = fs.readFileSync(helpersPath, 'utf-8');
    
    // Check for JSDoc comments
    expect(content).toContain('/**');
    expect(content).toContain('* @example');
    expect(content).toContain('* @param');
  });

  it('should have usage examples in documentation', () => {
    const fs = require('fs');
    const path = require('path');
    
    const guidePath = path.join(__dirname, 'E2E_HELPERS_GUIDE.md');
    const content = fs.readFileSync(guidePath, 'utf-8');
    
    // Check for examples
    expect(content).toContain('```typescript');
    expect(content).toContain('Example:');
    expect(content).toContain('await waitForElement');
    expect(content).toContain('await fillAndSubmitForm');
  });
});

